import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { db } from '../firebase'; // Firebase     
import {
  collection,
  addDoc,
  getDocs,
  doc,
  getDoc,
  query,
  where,
  orderBy
} from 'firebase/firestore';

function ApplicationForm() {
  const { id } = useParams(); //  ID URL  
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    name: '',
    studentId: '',
    phone: '',
    schoolType: '',
    //    dormitoryStatus dormitoryPhone ''     .
    dormitoryStatus: '', //  
    dormitoryPhone: '',
    password: '', //  /   
  });

  const [isClosed, setIsClosed] = useState(false);
  const [maxCapacity, setMaxCapacity] = useState(50); // Firestore   
  const [waitingNumber, setWaitingNumber] = useState(null); //   (Firebase     ,    )

  useEffect(() => {
    // 1.        
    const fetchSettings = async () => {
      try {
        const settingsRef = doc(db, 'settings', 'appSettings');
        const docSnap = await getDoc(settingsRef);

        if (docSnap.exists()) {
          const data = docSnap.data();
          setIsClosed(data.isClosed !== undefined ? data.isClosed : false);
          setMaxCapacity(data.maxCapacity || 50);
        } else {
          //      (    )
          console.log("No appSettings document found. Using default values.");
          setIsClosed(false);
          setMaxCapacity(50);
        }
      } catch (error) {
        console.error("Error fetching settings:", error);
        alert("     .");
      }
    };

    fetchSettings();
  }, []); //      

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    //   
    if (isClosed) {
      alert('  .  .');
      return;
    }

    if (
      !formData.name ||
      !formData.studentId ||
      !formData.phone ||
      !formData.schoolType ||
      !formData.password
    ) {
      alert(', , ,  ,    .');
      return;
    }

    //       /   
    if (
      formData.schoolType === '' &&
      (formData.dormitoryStatus === '' || formData.dormitoryStatus === '') &&
      !formData.dormitoryPhone
    ) {
      alert('    .');
      return;
    }

    //    (    )
    const applicationData = {
      name: formData.name,
      studentId: formData.studentId,
      phone: formData.phone,
      schoolType: formData.schoolType,
      //   dormitoryStatus dormitoryPhone ''    
      dormitoryStatus: formData.schoolType === '' ? formData.dormitoryStatus : '',
      dormitoryPhone: formData.schoolType === '' ? formData.dormitoryPhone : '',
      password: formData.password, //  Firestore  (   )
      eventId: id || '', // URL    ID
      timestamp: Date.now(), //   
    };

    try {
      // 1.  ,    
      //     (applications )
      const applicationsQuery = query(collection(db, 'applications'), where('studentId', '==', formData.studentId));
      const existingApplications = await getDocs(applicationsQuery);

      if (!existingApplications.empty) {
        alert(' .   .');
        return;
      }

      //      (waitingList )
      const waitingListQuery = query(collection(db, 'waitingList'), where('studentId', '==', formData.studentId));
      const existingWaitingList = await getDocs(waitingListQuery);

      if (!existingWaitingList.empty) {
        alert('   .   .');
        return;
      }

      // 2.     (applications   )
      const currentApplicationsSnapshot = await getDocs(collection(db, 'applications'));
      const currentApplicationsCount = currentApplicationsSnapshot.docs.length;

      if (currentApplicationsCount < maxCapacity) {
        //    : applications  
        await addDoc(collection(db, 'applications'), applicationData);
        alert(' . !');
        navigate('/'); //     
      } else {
        //  : waitingList  
        const addedDocRef = await addDoc(collection(db, 'waitingList'), applicationData);

        //    (Firestore     )
        const currentWaitingListSnapshot = await getDocs(query(collection(db, 'waitingList'), orderBy('timestamp', 'asc')));
        const currentWaitingList = currentWaitingListSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        
        //        
        const addedIndex = currentWaitingList.findIndex(item => item.id === addedDocRef.id);
        setWaitingNumber(addedIndex + 1); // 0  +1

        alert(`   .`);
        //         
      }

      //  
      setFormData({
        name: '',
        studentId: '',
        phone: '',
        schoolType: '',
        dormitoryStatus: '',
        dormitoryPhone: '',
        password: '',
      });

    } catch (error) {
      console.error('    :', error);
      alert('   : ' + error.message);
    }
  };

  return (
    <div>
      <h2> </h2>
      {isClosed ? (
        <p style={{ color: 'red', fontWeight: 'bold' }}>  .</p>
      ) : (
        <form onSubmit={handleSubmit}>
          <div>
            <label>
              :
              <input name="name" value={formData.name} onChange={handleChange} required />
            </label>
          </div>
          <div>
            <label>
              :
              <input name="studentId" value={formData.studentId} onChange={handleChange} required />
            </label>
          </div>
          <div>
            <label>
              :
              <input name="phone" value={formData.phone} onChange={handleChange} required />
            </label>
          </div>
          <div>
            <label>
               :
              <select name="schoolType" value={formData.schoolType} onChange={handleChange} required>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </label>
          </div>
          {/* ''   /    */}
          {formData.schoolType === '' && (
            <>
              <div>
                <label>
                  / :
                  <select name="dormitoryStatus" value={formData.dormitoryStatus} onChange={handleChange}>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                </label>
              </div>
              {(formData.dormitoryStatus === '' || formData.dormitoryStatus === '') && (
                <div>
                  <label>
                    / :
                    <input
                      type="text" //   text  tel
                      name="dormitoryPhone"
                      value={formData.dormitoryPhone}
                      onChange={handleChange}
                      required //  
                    />
                  </label>
                </div>
              )}
            </>
          )}
          <div>
            <label>
               (/  ):
              <input
                type="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                required
              />
            </label>
          </div>
          <button type="submit"></button>
        </form>
      )}
      {waitingNumber !== null && (
        <p style={{ marginTop: '20px', color: 'red', fontWeight: 'bold' }}>
              .   {waitingNumber}.
        </p>
      )}
    </div>
  );
}

export default ApplicationForm;