// src/pages/AdminLogin.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
// import { getAuth, signInWithEmailAndPassword } from 'firebase/auth'; // <--   getAuth 
import { signInWithEmailAndPassword } from 'firebase/auth'; // <--  
import { db, auth } from '../firebase'; // Firebase    (auth )
import { doc, getDoc } from 'firebase/firestore'; // Firestore   

function AdminLogin() {
  const navigate = useNavigate();

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleLogin = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;
      console.log("Firebase  :", user);

      const userDocRef = doc(db, 'users', user.uid);
      const userDocSnap = await getDoc(userDocRef);

      if (userDocSnap.exists() && userDocSnap.data().isAdmin === true) {
        setError('');
        alert('  !');
        navigate('/admin');
      } else {
        await auth.signOut();
        setError('   .');
        console.error("  :", user.uid);
      }
    } catch (firebaseError) {
      console.error("Firebase  :", firebaseError.code, firebaseError.message);
      let errorMessage = ' .  .';
      if (firebaseError.code === 'auth/invalid-email') {
        errorMessage = '   .';
      } else if (firebaseError.code === 'auth/user-not-found' || firebaseError.code === 'auth/wrong-password') {
        errorMessage = '    .';
      } else if (firebaseError.code === 'auth/too-many-requests') {
        errorMessage = '    .    .';
      }
      setError(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ maxWidth: '300px', margin: 'auto', paddingTop: '100px' }}>
      <h2> </h2>
      <form onSubmit={handleLogin}>
        <div>
          <label htmlFor="email"> ():</label>
          <input
            id="email"
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
            autoComplete="email"
          />
        </div>
        <div>
          <label htmlFor="password">:</label>
          <input
            id="password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
            autoComplete="current-password"
          />
        </div>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <button type="submit" disabled={loading}>
          {loading ? ' ...' : ''}
        </button>
      </form>
    </div>
  );
}

export default AdminLogin;