import { useNavigate, useParams } from 'react-router-dom';
import React, { useState } from 'react';
import { db } from '../firebase'; // Firebase     
import {
  collection,
  query,
  where,
  getDocs,
  deleteDoc,
  doc,
  updateDoc,
  orderBy
} from 'firebase/firestore';

function EventDetail() {
  const { id } = useParams(); // URL   ID 
  const navigate = useNavigate();

  const [queryData, setQueryData] = useState({ name: '', studentId: '' }); // 'query' -> 'queryData'   (state  query  )
  const [result, setResult] = useState(null); //  
  const [isEditing, setIsEditing] = useState(false); //   
  const [editFormData, setEditFormData] = useState({ name: '', studentId: '', phone: '', password: '' }); //     
  const [passwordInput, setPasswordInput] = useState(''); // /   
  const [isVerifyingCancel, setIsVerifyingCancel] = useState(false); //     

  //     
  const handleQueryInputChange = (e) => {
    const { name, value } = e.target;
    setQueryData(prev => ({ ...prev, [name]: value }));
  };

  // ---   ---
  const handleQuery = async () => {
    //    
    if (!queryData.name || !queryData.studentId) {
      alert('   .');
      return;
    }

    try {
      let foundApp = null;
      let isWaiting = false;
      let waitingNumber = null;

      // 1. 'applications'  
      const applicationsQuery = query(
        collection(db, 'applications'),
        where('name', '==', queryData.name),
        where('studentId', '==', queryData.studentId),
        where('eventId', '==', id)
      );
      const applicationsSnapshot = await getDocs(applicationsQuery);

      if (!applicationsSnapshot.empty) {
        foundApp = { id: applicationsSnapshot.docs[0].id, ...applicationsSnapshot.docs[0].data() };
        isWaiting = false;
      } else {
        // 2. 'waitingList'  
        const waitingListQuery = query(
          collection(db, 'waitingList'),
          where('name', '==', queryData.name),
          where('studentId', '==', queryData.studentId),
          where('eventId', '==', id)
        );
        const waitingListSnapshot = await getDocs(waitingListQuery);

        if (!waitingListSnapshot.empty) {
          foundApp = { id: waitingListSnapshot.docs[0].id, ...waitingListSnapshot.docs[0].data() };
          isWaiting = true;

          //   :     timestamp      
          const allWaitingSnapshot = await getDocs(query(collection(db, 'waitingList'), orderBy('timestamp', 'asc')));
          const allWaiting = allWaitingSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
          const foundIndex = allWaiting.findIndex(app => app.id === foundApp.id);
          waitingNumber = foundIndex !== -1 ? foundIndex + 1 : null;
        }
      }

      if (foundApp) {
        setResult({ ...foundApp, isWaiting, waitingNumber });
        setEditFormData({
          name: foundApp.name,
          studentId: foundApp.studentId,
          phone: foundApp.phone,
          password: foundApp.password || '',
        });
        setIsEditing(false); //     
        setIsVerifyingCancel(false); //     
        setPasswordInput(''); //   
      } else {
        alert('    .    .');
        setResult(null);
        setIsEditing(false);
        setIsVerifyingCancel(false);
        setPasswordInput('');
      }
    } catch (error) {
      console.error('   :', error);
      alert('     : ' + error.message);
    }
  };

  //   
  const handleApplyClick = () => {
    navigate(`/event/${id}/apply`);
  };

  // ---   ---
  //        
  const handleCancelClick = () => {
    if (!result) return;
    setIsVerifyingCancel(true); //    
    setPasswordInput(''); //   
  };

  //     
  const handlePasswordInputChange = (e) => {
    setPasswordInput(e.target.value);
  };

  //    (   )
  const handleConfirmCancel = async () => {
    if (!passwordInput) {
      alert(' .');
      return;
    }

    if (passwordInput !== result.password) {
      alert('  .');
      return;
    }

    if (window.confirm('/   ?')) {
      try {
        const collectionName = result.isWaiting ? 'waitingList' : 'applications';
        await deleteDoc(doc(db, collectionName, result.id)); // Firestore  

        alert('/   .');
        setResult(null); //  
        setIsEditing(false);
        setIsVerifyingCancel(false);
        setPasswordInput('');
      } catch (error) {
        console.error('    :', error);
        alert('   : ' + error.message);
      }
    }
  };

  // ---   ---
  //       
  const handleEditClick = () => {
    setIsEditing(true);
    setIsVerifyingCancel(false); //      
    //      
    setEditFormData({
      name: result.name,
      studentId: result.studentId,
      phone: result.phone,
      password: result.password || '',
    });
  };

  //    
  const handleEditChange = (e) => {
    const { name, value } = e.target;
    setEditFormData(prev => ({ ...prev, [name]: value }));
  };

  //  
  const handleSaveEdit = async () => {
    //     
    if (!editFormData.name || !editFormData.phone || !editFormData.password) {
      alert(', ,  .');
      return;
    }

    if (editFormData.password !== result.password) {
      alert('   .  .');
      return;
    }

    if (window.confirm('  ?')) {
      try {
        const collectionName = result.isWaiting ? 'waitingList' : 'applications';
        const docRef = doc(db, collectionName, result.id); //   

        //   ( eventId  )
        const updatedData = {
          name: editFormData.name,
          phone: editFormData.phone,
          //    ,    (   )
          password: editFormData.password,
        };

        await updateDoc(docRef, updatedData); // Firestore  

        alert('  .');
        // UI   result  
        setResult(prevResult => ({
          ...prevResult,
          name: editFormData.name,
          phone: editFormData.phone,
          password: editFormData.password,
        }));
        setIsEditing(false); //   
      } catch (error) {
        console.error('    :', error);
        alert('    : ' + error.message);
      }
    }
  };

  //   (      )
  const handleCancelEdit = () => {
    setIsEditing(false);
    //     result  
    setEditFormData({
      name: result.name,
      studentId: result.studentId,
      phone: result.phone,
      password: result.password || '',
    });
  };

  return (
    <div>
      <h2>/</h2>
      <div>
        <button onClick={handleApplyClick}></button>
      </div>
      <div style={{ marginTop: '20px' }}>
        <h3>   </h3>
        <input
          name="name"
          placeholder=""
          value={queryData.name}
          onChange={handleQueryInputChange}
          style={{ marginRight: '10px' }}
        />
        <input
          name="studentId"
          placeholder=""
          value={queryData.studentId}
          onChange={handleQueryInputChange}
          style={{ marginRight: '10px' }}
        />
        <button onClick={handleQuery}></button>
      </div>

      {result && (
        <div style={{ marginTop: 20, border: '1px solid #ccc', padding: '15px', borderRadius: '8px' }}>
          <h3>  </h3>
          {isEditing ? (
            <div>
              <div>
                <label>
                  :
                  <input
                    name="name"
                    value={editFormData.name}
                    onChange={handleEditChange}
                    required
                  />
                </label>
              </div>
              <div>
                <label>
                  :
                  <input name="studentId" value={editFormData.studentId} disabled style={{ backgroundColor: '#f0f0f0' }} />
                </label>
              </div>
              <div>
                <label>
                  :
                  <input
                    name="phone"
                    value={editFormData.phone}
                    onChange={handleEditChange}
                    required
                  />
                </label>
              </div>
              <div>
                <label>
                  :
                  <input
                    type="password"
                    name="password"
                    value={editFormData.password}
                    onChange={handleEditChange}
                    required
                  />
                </label>
              </div>
              <button onClick={handleSaveEdit} style={{ marginRight: '10px' }}></button>
              <button onClick={handleCancelEdit}> </button>
            </div>
          ) : (
            <div>
              <p><strong>:</strong> {result.name}</p>
              <p><strong>:</strong> {result.studentId}</p>
              <p><strong>:</strong> {result.phone}</p>
              <p>
                <strong>:</strong>{' '}
                {result.isWaiting ? ` ( : ${result.waitingNumber})` : ' '}
              </p>
              {result.schoolType && <p><strong> :</strong> {result.schoolType}</p>}
              {result.dormitoryStatus && result.dormitoryStatus !== '' && (
                <p><strong>/:</strong> {result.dormitoryStatus}</p>
              )}
              {result.dormitoryPhone && (
                <p><strong>/ :</strong> {result.dormitoryPhone}</p>
              )}
              <p><strong> :</strong> {new Date(result.timestamp).toLocaleString()}</p>

              <div style={{ marginTop: '15px' }}>
                <button onClick={handleEditClick} style={{ marginRight: '10px' }}></button>
                <button onClick={handleCancelClick}></button>
              </div>
            </div>
          )}

          {isVerifyingCancel && (
            <div style={{ marginTop: '15px', padding: '10px', borderTop: '1px solid #eee' }}>
              <p>   .</p>
              <input
                type="password"
                placeholder=" "
                value={passwordInput}
                onChange={handlePasswordInputChange}
                style={{ marginRight: '10px' }}
              />
              <button onClick={handleConfirmCancel} style={{ marginRight: '10px', backgroundColor: 'red', color: 'white' }}> </button>
              <button onClick={() => setIsVerifyingCancel(false)}></button>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default EventDetail;
