import React, { useState, useEffect } from 'react';
// import { useNavigate } from 'react-router-dom'; //   AdminPage   
import { db } from '../firebase'; // Firebase     
import {
  collection,
  doc,
  deleteDoc,
  updateDoc,
  onSnapshot,
  addDoc,
  setDoc,
  query,
  orderBy
} from 'firebase/firestore';

function AdminPage() {
  // const navigate = useNavigate(); // : PrivateRoute  

  const [applications, setApplications] = useState([]);
  const [waitingList, setWaitingList] = useState([]);
  const [maxCapacity, setMaxCapacity] = useState(50);
  const [selectedSchool, setSelectedSchool] = useState('all');
  const [selectedDormType, setSelectedDormType] = useState('all');
  const [searchName, setSearchName] = useState('');
  const [isClosed, setIsClosed] = useState(false);

  const [settingsDocRef, setSettingsDocRef] = useState(null);

  useEffect(() => {
    const unsubscribeApps = onSnapshot(query(collection(db, 'applications'), orderBy('timestamp', 'asc')), (snapshot) => {
      const apps = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setApplications(apps);
    });

    const unsubscribeWaiting = onSnapshot(query(collection(db, 'waitingList'), orderBy('timestamp', 'asc')), (snapshot) => {
      const waiting = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setWaitingList(waiting);
    });

    const settingsRef = doc(db, 'settings', 'appSettings');
    setSettingsDocRef(settingsRef);

    const unsubscribeSettings = onSnapshot(settingsRef, (docSnap) => {
      if (docSnap.exists()) {
        const data = docSnap.data();
        setMaxCapacity(data.maxCapacity || 50);
        setIsClosed(data.isClosed !== undefined ? data.isClosed : false);
      } else {
        console.log("No appSettings document found, creating with default values.");
        setDoc(settingsRef, { maxCapacity: 50, isClosed: false, timestamp: Date.now() })
          .then(() => {
            setMaxCapacity(50);
            setIsClosed(false);
          })
          .catch(error => console.error("Error creating appSettings document:", error));
      }
    });

    return () => {
      unsubscribeApps();
      unsubscribeWaiting();
      unsubscribeSettings();
    };
  }, []);

  const handleCancel = async (appToCancel, fromWaiting = false) => {
    if (window.confirm('   ?')) {
      try {
        if (fromWaiting) {
          await deleteDoc(doc(db, 'waitingList', appToCancel.id));
          alert('  .');
        } else {
          await deleteDoc(doc(db, 'applications', appToCancel.id));
          alert(' .');

          if (waitingList.length > 0) {
            const nextApplicant = waitingList[0];
            await deleteDoc(doc(db, 'waitingList', nextApplicant.id));

            const { id, ...dataToMove } = nextApplicant;

            await addDoc(collection(db, 'applications'), {
              ...dataToMove,
              movedFromWaiting: true,
              timestamp: Date.now()
            });
            alert(`${nextApplicant.name}     .`);
          }
        }
      } catch (error) {
        console.error('    :', error);
        alert('   : ' + error.message);
      }
    }
  };

  const toggleClose = async () => {
    if (settingsDocRef) {
      try {
        await updateDoc(settingsDocRef, { isClosed: !isClosed });
      } catch (error) {
        console.error(' /     :', error);
        alert('    : ' + error.message);
      }
    }
  };

  const handleMaxCapacityChange = async (e) => {
    const val = parseInt(e.target.value, 10);
    if (!isNaN(val) && val > 0 && settingsDocRef) {
      try {
        await updateDoc(settingsDocRef, { maxCapacity: val });
      } catch (error) {
        console.error('     :', error);
        alert('     : ' + error.message);
      }
    }
  };

  const schools = ['all', '', '', '', ''];
  const dormTypes = ['all', '', '', ''];

  const filteredApplications = applications.filter((app) => {
    if (selectedSchool !== 'all' && app.schoolType !== selectedSchool) return false;
    if (selectedSchool === '' && selectedDormType !== 'all' && app.dormitoryStatus !== selectedDormType) return false;
    if (searchName && !app.name.includes(searchName)) return false;
    return true;
  });

  const filteredWaiting = waitingList.filter((app) => {
    if (selectedSchool !== 'all' && app.schoolType !== selectedSchool) return false;
    if (selectedSchool === '' && selectedDormType !== 'all' && app.dormitoryStatus !== selectedDormType) return false;
    if (searchName && !app.name.includes(searchName)) return false;
    return true;
  });

  // ---     ---
  const downloadAsExcel = (data, filename = '_') => {
    // CSV   ( )
    const headers = [
      "", "", "", " ", "/",
      "/ ", " ID", " "
    ];

    //  CSV   ( +   )
    const csvContent = [
      headers.join(','), //  
      ...data.map(row =>
        [
          `"${row.name}"`, //  (      )
          `"${row.studentId}"`, // 
          `"${row.phone}"`, // 
          `"${row.schoolType || ''}"`, //  
          `"${row.dormitoryStatus || '-'}"`, // /
          `"${row.dormitoryPhone || '-'}"`, // / 
          `"${row.eventId || '-'}"`, //  ID
          `"${new Date(row.timestamp).toLocaleString()}"` //  
        ].join(',')
      )
    ].join('\n');

    // Blob      
    const blob = new Blob(["\ufeff", csvContent], { type: 'text/csv;charset=utf-8;' }); // BOM    
    const link = document.createElement('a');
    if (link.download !== undefined) { // HTML5 download   
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', `${filename}_${new Date().toLocaleDateString('ko-KR')}.csv`);
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  return (
    <div>
      {/* <h2> </h2> */}
      {/*   AdminDashboard  */}

      <div style={{ margin: '20px 0' }}>
        <button
          onClick={toggleClose}
          style={{
            backgroundColor: isClosed ? 'red' : 'green',
            color: 'white',
            padding: '8px 16px',
            border: 'none',
            cursor: 'pointer',
          }}
        >
          {isClosed ? ' ' : ' '}
        </button>
        <span style={{ marginLeft: '15px', fontWeight: 'bold' }}>
           : {isClosed ? ' ' : ' '}
        </span>
      </div>

      <div style={{ margin: '20px 0' }}>
        <label>
             :{' '}
          <input
            type="number"
            value={maxCapacity}
            onChange={handleMaxCapacityChange}
            min={1}
            style={{ width: '80px' }}
          />
        </label>
        <span style={{ marginLeft: '20px' }}>
            : {applications.length} /  {maxCapacity}
        </span>
      </div>

      <div style={{ margin: '10px 0' }}>
        <input
          type="text"
          placeholder=" "
          value={searchName}
          onChange={(e) => setSearchName(e.target.value)}
          style={{ padding: '6px', width: '200px' }}
        />
      </div>

      <div style={{ margin: '20px 0' }}>
        <span> : </span>
        {schools.map((school) => (
          <button
            key={school}
            onClick={() => {
              setSelectedSchool(school);
              setSelectedDormType('all');
            }}
            style={{ marginRight: '10px', fontWeight: selectedSchool === school ? 'bold' : 'normal' }}
          >
            {school === 'all' ? '' : school}
          </button>
        ))}
      </div>

      {selectedSchool === '' && (
        <div style={{ margin: '10px 0' }}>
          <span>/ : </span>
          {dormTypes.map((type) => (
            <button
              key={type}
              onClick={() => setSelectedDormType(type)}
              style={{ marginRight: '10px', fontWeight: selectedDormType === type ? 'bold' : 'normal' }}
            >
              {type === 'all' ? '' : type}
            </button>
          ))}
        </div>
      )}

      <h3>  ({filteredApplications.length})</h3>
      {/*    :   */}
      <button
        onClick={() => downloadAsExcel(filteredApplications, '_')}
        style={{ marginBottom: '10px', padding: '8px 15px', backgroundColor: '#28a745', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}
      >
           
      </button>

      {filteredApplications.length === 0 ? (
        <p> .</p>
      ) : (
        <table
          border="1"
          cellPadding="5"
          cellSpacing="0"
          style={{ width: '100%', borderCollapse: 'collapse' }}
        >
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th> </th>
              <th>/</th>
              <th>/ </th>
              <th> ID</th>
              <th> </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {filteredApplications.map((app) => (
              <tr key={app.id}>
                <td>{app.name}</td>
                <td>{app.studentId}</td>
                <td>{app.phone}</td>
                <td>{app.schoolType || ''}</td>
                <td>{app.dormitoryStatus || '-'}</td>
                <td>{app.dormitoryPhone || '-'}</td>
                <td>{app.eventId}</td>
                <td>{new Date(app.timestamp).toLocaleString()}</td>
                <td>
                  <button onClick={() => handleCancel(app, false)}></button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}

      <h3 style={{ marginTop: '40px' }}>  ({filteredWaiting.length})</h3>
      {/*    :   */}
      <button
        onClick={() => downloadAsExcel(filteredWaiting, '_')}
        style={{ marginBottom: '10px', padding: '8px 15px', backgroundColor: '#28a745', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}
      >
           
      </button>

      {filteredWaiting.length === 0 ? (
        <p> .</p>
      ) : (
        <table
          border="1"
          cellPadding="5"
          cellSpacing="0"
          style={{ width: '100%', borderCollapse: 'collapse' }}
        >
          <thead>
            <tr>
              <th> </th>
              <th></th>
              <th></th>
              <th></th>
              <th> </th>
              <th>/</th>
              <th>/ </th>
              <th> </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {filteredWaiting.map((app, idx) => (
              <tr key={app.id}>
                <td>{idx + 1}</td>
                <td>{app.name}</td>
                <td>{app.studentId}</td>
                <td>{app.phone}</td>
                <td>{app.schoolType || ''}</td>
                <td>{app.dormitoryStatus || '-'}</td>
                <td>{app.dormitoryPhone || '-'}</td>
                <td>{new Date(app.timestamp).toLocaleString()}</td>
                <td>
                  <button onClick={() => handleCancel(app, true)}></button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default AdminPage;